@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap');

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-size: 1em;
}

body {
  background-color: #05445E;
}

#loginPageMain {
  background-color: #FEFDFE;
  display: flex;
  justify-content: center;
  margin-top: 150px;
  padding: 3%;
}

#loginFormContainer img {
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 260px;
  margin-bottom: 2%;
}

#loginFormContainer {
  background-color: #FEFDFE;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  height: 30%;
}

#loginForm {
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  flex-direction: column;
  width: 45%;
  align-items: center;
  padding: 4% 8%;
}

#loginForm label {
  text-align: center;
  width: 400px;
}

#loginForm input[type = "email"], #loginForm input[type = "password"]  {
  border: solid 1.5px #189AB4;
  border-radius: 4px;
  font-size: 1.15em;
  width: 55%;
  margin-bottom: 15px;
  padding: 2%;
  transition: 0.4s width ease-in-out;
}

#loginForm input[type = "email"]:focus, #loginForm input[type = "password"]:focus  {
  background-color: #D4F1F4;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  outline: none;
  width: 90%;
}

#loginBtn {
  background-color: #75E6DA;
  color: #05445E;
  border: none;
  border-radius: 5px;
  width: 40%;
  cursor: pointer;
  font-size: 1.1em;
  padding: 2.5%;
  transition: 0.4s width ease-in-out;
}

#loginBtn:hover {
  background-color: #D4F1F4;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 55%;
}

#loginBtn:disabled {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  border: none;
  border-radius: 8px;
  width: 30%;
  font-size: 1em;
  padding: 2.5%;
  transition: 0.4s width ease-in-out;
}

#loginBtn:disabled:hover {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  cursor: default;
  box-shadow: none;
  width: 30%;
}

#developerLink {
  color: #05445E;
  font-size: 1.2em;
  margin-top: 5%;
}

#developerLink a {
  text-decoration: none;
  color: #0CA068;
  font-weight: 600;
  transition-duration: 0.3s;
}

#developerLink a:hover {
  color: #56c49a;
  font-weight: 600;
}