* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-size: 1em;
}

body {
  background-color: #05445E;
}

#loginPageMain {
  background-color: #FEFDFE;
  display: flex;
  justify-content: center;
  margin-top: 150px;
  padding: 3%;
}

#loginFormContainer img {
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 260px;
  margin-bottom: 2%;
}

#loginFormContainer {
  background-color: #FEFDFE;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  height: 30%;
}

#loginForm {
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  flex-direction: column;
  width: 45%;
  align-items: center;
  padding: 4% 8%;
}

#loginForm label {
  text-align: center;
  width: 400px;
}

#loginForm input[type = "email"], #loginForm input[type = "password"]  {
  border: solid 1.5px #189AB4;
  border-radius: 4px;
  font-size: 1.15em;
  width: 55%;
  margin-bottom: 15px;
  padding: 2%;
  transition: 0.4s width ease-in-out;
}

#loginForm input[type = "email"]:focus, #loginForm input[type = "password"]:focus  {
  background-color: #D4F1F4;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  outline: none;
  width: 90%;
}

#loginBtn {
  background-color: #75E6DA;
  color: #05445E;
  border: none;
  border-radius: 5px;
  width: 40%;
  cursor: pointer;
  font-size: 1.1em;
  padding: 2.5%;
  transition: 0.4s width ease-in-out;
}

#loginBtn:hover {
  background-color: #D4F1F4;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 55%;
}

#loginBtn:disabled {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  border: none;
  border-radius: 8px;
  width: 30%;
  font-size: 1em;
  padding: 2.5%;
  transition: 0.4s width ease-in-out;
}

#loginBtn:disabled:hover {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  cursor: default;
  box-shadow: none;
  width: 30%;
}

#developerLink {
  color: #05445E;
  font-size: 1.2em;
  margin-top: 5%;
}

#developerLink a {
  text-decoration: none;
  color: #0CA068;
  font-weight: 600;
  transition-duration: 0.3s;
}

#developerLink a:hover {
  color: #56c49a;
  font-weight: 600;
}
* {
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
  font-size: 1em;
}

#websiteHeader {
  background-image: linear-gradient(#189AB4, #6cd8e4);
  border: solid 2px black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2%;
  padding: 1.5%;
}

#websiteHeader img {
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 150px;
}

#headerImportantInfo {
  border: solid 2px #05445E;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 40%;
  font-size: 1.2em;
}

#headerEmailOutput {
  color: ghostwhite;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
}

#logOutBtn {
  background-color: #75E6DA;
  color: #05445E;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 1%;
  transition: 0.3s ease-in-out;
}

#logOutBtn:hover {
  background-color: #f8cfc1;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-size: 1em;
}

#addExpenseForm {
  background-color: #D4F1F4;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  justify-content: space-evenly;
  padding: 1%;
}

#addExpenseForm label {
  background-image: linear-gradient(to bottom right, #189AB4, #b8eff5);
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  color: #05445E;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 15%;
  font-size: 1.1em;
  font-weight: 600;
  padding: 0.3% 0;
}

/* <ExpenseValue /> e <ExpenseDescription /> */

#addExpenseForm input[type = "number"], #addExpenseForm input[type = "text"]  {
  border: solid 1.5px #189AB4;
  border-radius: 4px;
  font-size: 1.15em;
  width: 80%;
  margin-top: 2.5%;
  padding: 2%;
  transition: 0.3s ease-in-out;
}

#addExpenseForm input[type = "number"]:focus, #addExpenseForm input[type = "text"]:focus  {
  background-color: #c0f5f1;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  outline: none;
}

/* <CurrenciesSelect />, <PaymentTypeSelect /> e <ExpenseTag /> */

#addExpenseForm select {
  background-color: ghostwhite;
  border: solid 1.5px #189AB4;
  border-radius: 4px;
  font-size: 1.15em;
  cursor: pointer;
  width: 80%;
  margin-top: 2.5%;
  padding: 2%;
}

#addExpenseForm select:focus  {
  background-color: #c0f5f1;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  outline: none;
}

/* Botão de adicionar despesa */

#addExpenseBtn {
  background-color: #75E6DA;
  border: none;
  border-radius: 5px;
  align-self: center;
  height: 10%;
  cursor: pointer;
  font-size: 1.1em;
  padding: 1%;
  transition: 0.2s ease-in-out;
}

#addExpenseBtn:hover {
  background-color: #A3EBB1;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-size: 1em;
}

#expensesTable {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 90%;
  margin: 2% 5%;
}

#expensesTable thead {
  background-image: linear-gradient(to bottom right, #189AB4, #b8eff5);
  color: #05445E;
  font-size: 1.3em;
  font-weight: 600;
}

#expensesTable tbody {
  background-color: rgb(250, 242, 226);
  color: #05445E;
  font-size: 1.2em;
  text-align: center;
}

#expensesTable tbody button {
  background-color: #75E6DA;
  border: none;
  border-radius: 5px;
  align-self: center;
  cursor: pointer;
  font-size: 1.1em;
  padding: 2%;
  transition: 0.2s ease-in-out;
}

#expensesTable tbody button:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: #f5b7a3;
  color: black;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
